  left: 8px;
}
.input-custom__item label {
  position: absolute;
  font-size: 1.2rem;
  top: 5px;
  left: 35px;
  color: #1A4A8C;
  z-index: 9;
}
.input-custom__item::before {
  position: absolute;
  content: "";
  background-image: url("../assets/icons/icon-danger.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 30px;
  height: 20px;
  z-index: 9;
  right: 0;
  opacity: 0;
}
.input-custom__options {
  display: flex;
  gap: 5px;
  width: 100%;
}
.input-custom__no-write {
  position: relative;
}
.input-custom__no-write::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-color: transparent;
  left: 0;
  top: 0;
  z-index: 99;
}
.input-custom__block.error input {
  border-color: #E92222;
}
.input-custom__block.error label {
  color: #E92222;
}
.input-custom__block.error .error-message {
  display: block;
}
.input-custom__block.error .input-custom__item::before {
  opacity: 1;
}

@media (max-width: 992px) {
  .input-custom__ctn {
    flex-wrap: wrap;
    gap: 10px;
  }
  .input-custom__options {
    flex-wrap: wrap;
    justify-content: center;
  }
  .input-custom__block {
    width: calc(50% - 2.5px);
  }
}
@media (max-width: 576px) {
  .input-custom__block {
    width: 100%;
  }
  .input-custom__block:nth-child(2)::before {
    left: unset;
    top: -20px;
    right: 20px;
  }
  .input-custom__calendar {
    width: calc(50% - 2.5px);
  }
}
.btn {
  color: #fff;
  font-size: 1.8rem;
  font-weight: 700;
  background-color: #00BDD6;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  min-width: 150px;
  width: fit-content;
  padding: 12.5px 20px;
  cursor: pointer;
  transition: 0.3s background-color ease;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn:hover {
  background-color: #559FC7;
}

.btn-s {
  color: #1A4A8C;
  font-size: 1.8rem;
  font-weight: 700;
  background-color: #fff;
  border-radius: 10px;
  min-width: 150px;
  width: fit-content;
  padding: 11px 20px;
  cursor: pointer;
  transition: 0.3s background-color ease;
  box-sizing: border-box;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-s:hover {
  background-color: #DEF3FF;
}

.aos-custom {
  opacity: 0;
}
.aos-custom-fade-up {
  opacity: 0;
  transform: translateY(40px);
  transition: 1s ease;
  transition-property: opacity, transform;
}
.aos-custom-fade-left {
  transform: translateX(-100px);
  transition: 1s ease;
  transition-property: opacity, transform;
}
.aos-custom-fade-right {
  transform: translateX(100px);
  transition: 1s ease;
  transition-property: opacity, transform;
}
.aos-custom-scale {
  transform: scaleX(0);
  transition: 1s ease transform;
}
.aos-custom-scaleX, .aos-custom-scaleXreverse {
  transform: scale(0);
  position: relative;
  transition: 1s ease all;
}
.aos-custom-scaleX {
  left: -150px;
}
.aos-custom-scaleXreverse {
  right: -150px;
}
.aos-custom-rotate {
  transform: scale(0.5);
  opacity: 0;
  transition: 1s ease all;
}
.aos-custom.aos-animate {
  opacity: 1;
}

.aos-activate.aos-animate .aos-custom-scale {
  transform: scale(100%);
}
.aos-activate.aos-animate .aos-custom-scaleX {
  transform: scale(1);
  left: 0;
}
.aos-activate.aos-animate .aos-custom-scaleXreverse {
  transform: scale(1);
  right: 0;
}
.aos-activate.aos-animate .aos-custom-fade-left,
.aos-activate.aos-animate .aos-custom-fade-right {
  transform: translateX(0);
}
.aos-activate.aos-animate .aos-custom-fade-up {
  transform: translateY(0);
}
.aos-activate.aos-animate .aos-custom-rotate {
  transform: scale(1);
  opacity: 1;
}
.aos-activate.aos-animate .aos-custom-fade-up,
.aos-activate.aos-animate .aos-custom-fade-left,
.aos-activate.aos-animate .aos-custom-fade-right {
  opacity: 1;
}

.filters-block.aos-animate {
  opacity: 1;
  transform: translateY(0);
}

.home-welcome h1, .home-welcome p {
  opacity: 0;
  transform: translateY(100px);
  transition: 1s ease;
  transition-property: opacity, transform;
}

.home-welcome.aos-animate h1, .home-welcome.aos-animate p {
  opacity: 1;
  transform: translateY(0);
}
.home-welcome.aos-animate p {
  transition-delay: 0.25s;
}

.header-options .header-options__item {
  opacity: 0;
  transform: translateY(50px);
  transition: 1s ease;
  transition-property: opacity, transform;
}

.header-options.aos-animate .header-options__item {
  opacity: 1;
  transform: translateY(0);
}
.header-options.aos-animate .header-options__item:nth-child(2) {
  transition-delay: 0.1s;
}
.header-options.aos-animate .header-options__item:nth-child(3) {
  transition-delay: 0.2s;
}
.header-options.aos-animate .header-options__item:nth-child(4) {
  transition-delay: 0.4s;
}
.header-options.aos-animate .header-options__item:nth-child(5) {
  transition-delay: 0.5s;
}
.header-options.aos-animate .header-options__item:nth-child(6) {
  transition-delay: 0.6s;
}
.header-options.aos-animate .header-options__item:nth-child(7) {
  transition-delay: 0.7s;
}
.header-options.aos-animate .header-options__item:nth-child(8) {
  transition-delay: 0.8s;
}
.header-options.aos-animate .header-options__item:nth-child(9) {
  transition-delay: 0.9s;
}
.header-options.aos-animate .header-options__item:nth-child(10) {
  transition-delay: 1s;
}
.header-options.aos-animate .header-options__item:nth-child(11) {
