}

@media (max-width: 992px) {
  .filters-block {
    transition-delay: 1.2s;
  }
}
@keyframes entry {
  0% {
    opacity: 0;
    transform: scale(1.8) translateY(-0.75em);
  }
  20% {
    opacity: 1;
  }
  89% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: translateZ(-1000em);
  }
}